import React, { Component } from 'react';
import ReactDOM from 'react-dom';
import PageCustomer from './components/pages/page-customer';
import PageCustomerCode from './components/pages/page-customer-code';

require("../style/my_styles.scss");

class App extends Component {
	constructor(props) {
		super(props);
//		this.state = {
//		};
	}

	render() {
		return (

			<div className="container-all">
				<PageCustomerCode></PageCustomerCode>
			 	<PageCustomer></PageCustomer>
			</div>
			);
	}
}
// Ponerlo en el dom 
// <PageHome pageData={this.state.pageDataHome}/>
ReactDOM.render(<App />, document.querySelector('.main-container'));

