import React from 'react';
import DownloadApp from './../download-app';
import Star from './../star';
import Waiter from './../waiter';
import Ticket from './../ticket';
import Cancel from './../cancel';

const PageLogin = (props) => {
	return (
		<div className="page-customer actions">
			<div className="top">
				<img id="logo" src="\images\logo_ebell.svg" />
			</div>
			<div className="middle"> 
				<div className="info">
					<label>MESA 30</label>
				</div>
				<img id="message" className="message" src="\images\message.svg" />
			</div> 
			<div className="bottom">
				<div className="actions-container">
					<div className="actions">
						<Waiter id="waiter"></Waiter>
						<Ticket id="ticket"></Ticket>
						<Cancel id="cancel"></Cancel>
					</div>
					<div className="labels">
						<label className="action-label">MOZO</label>
						<label className="action-label">CUENTA</label>
						<label className="action-label">CANCELAR</label>
					</div>
				</div>
				<div className="footer">
					<div className="score">
						<div className="score-container">
							<p>PUNTUAR MOZO</p>
							<div className="rating">
								<Star></Star>
								<Star></Star>
								<Star></Star>
								<Star></Star>
								<Star></Star>
							</div>
						</div>
					</div>
					<DownloadApp></DownloadApp>
				</div>
			</div>
		</div>
		);
}

export default PageLogin;

